/* 最大宽度 */
.page {
  padding: 32px;
  text-align: center;
}
/* 头部 */
.sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 16px 32px;
  align-items: center;
  border-bottom: 1px solid #f8f8f8;
}
.sidebar-header .icon {
  color: #2f8cf0;
}
.sidebar-header-title {
  color: #515a6e;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
}
.sidebar-header-more {
  font-size: 18px;
  color: #989898;
  cursor: pointer;
}
.sidebar-header-more::after {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  border-top: 1px solid #D9D9D9;
  border-right: 1px solid #D9D9D9;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.sidebar-header-more:hover {
  color: #2d8cf0;
  text-decoration: underline;
}
.v-note-wrapper {
  box-shadow: none !important;
}
.v-note-wrapper .v-note-panel {
  box-shadow: none !important;
}
.v-note-wrapper .v-note-panel .v-note-show .v-show-content,
.v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  background: #fff !important;
}
.v-note-wrapper .v-note-panel .v-note-edit.divarea-wrapper .content-input-wrapper {
  padding: 0 !important;
}
.v-note-wrapper .v-note-panel .v-note-show .v-show-content,
.v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  padding: 0 !important;
}
.column {
  background: #fff;
  margin-bottom: 32px;
  border-radius: 10px;
  overflow: hidden;
}
.column-item {
  padding: 32px;
  display: flex;
  box-shadow: 1px 2px 3px #f6f6f6;
}
.column-images {
  width: 320px;
  margin-right: 32px;
}
.column-images img {
  width: 100%;
  border-radius: 6px;
}
.column-info {
  flex: 1;
}
.column-title {
  color: #17233d;
  font-size: 26px;
  font-weight: 500;
}
.column-desc {
  color: #808695;
  font-size: 18px;
  padding: 16px 0;
}
.column-intro-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.column-intro {
  display: flex;
  align-items: center;
}
.column-intro li {
  display: inline-block;
  font-size: 16px;
  color: #9ea7b4;
}
@media screen and (min-width: 200px) and (max-width: 750px) {
  .column-intro {
    display: none;
  }
}
